/**
 * The task options
 */
export interface ITaskOptions<T = unknown> {
    /**
     * Should this task be executed
     */
    shouldExecute: boolean;

    options?: T;
}
/**
 * Interface describing a task runner set of options
 */
export interface ITaskRunnerOptions {
    [taskName: string]: ITaskOptions;
}
